.feedback {
  background-color: $fancy-color;
}

.feedback__wrapper {
  @include container;

  display: flex;
  justify-content: flex-end;
}

.feedback__content {
  max-width: 573px;

  h3 {
    margin-top: 0;
    margin-bottom: 11px;

    font-family: "Roboto-Condensed", "Arial", sans-serif;
    font-weight: 400;
    font-size: 36px;
    line-height: 51px;
    color: $basic-white;
  }

  p {
    margin-top: 0;
    padding-left: 4px;

    line-height: 21px;
    color: $basic-white;
  }
}

.feedback__form {
  margin-top: 51px;
  padding-left: 3px;

  ul {
    display: flex;
    flex-wrap: wrap;

    margin: 0;
    padding: 0;

    list-style: none;
  }
}
